.admin-left-product{
    display: none;
    flex-direction: column;
    width: 400px;
}
.background{
    background-image: url(./../../assets/Img/MainBackground.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 1000px;
    z-index: -1;
    margin: 0 auto;
}
.admin{
    margin: 0 auto;
    margin-top: 100px;
    background-color: white;
    width: 700px;
    background-repeat: no-repeat;
    background-size: auto;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 70px;
    padding-right: 30px;
    display: flex;
    border-radius: 25px;
}
.admin-left-input{
    background: rgba(255, 255, 255, 0.4);
    border: 0.5px solid #C0C6C9;
    box-sizing: border-box;
    border-radius: 8px;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 248px;
    padding-left: 15px;
}
.admin-left-product-btn{
    width: 87px;
    margin-left: 80px;
    margin-top: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    background: linear-gradient(180deg, rgba(100, 98, 186, 0.666) 0%, rgba(78, 76, 173, 0.707) 100%);
    border-radius: 8px;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    color: #FFFFFF;
    border : 3px solid transparent;
    cursor: pointer;
    transition: 1s;
    margin-bottom: 10px;
}
.admin-left-select{
    background: rgba(255, 255, 255, 0.4);
    border: 0.5px solid #C0C6C9;
    box-sizing: border-box;
    border-radius: 8px;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 248px;
    padding-left: 15px;
    outline: transparent;
}
.admin-left-top{
    display: flex;
    justify-content: space-around;
    margin-right:100px;
}
.admin-left-market{
    display: flex;
    flex-direction: column;
    width: 400px;
    
}
.admin-left-market-input{
    background: rgba(255, 255, 255, 0.4);
    border: 0.5px solid #C0C6C9;
    box-sizing: border-box;
    border-radius: 8px;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 248px;
    padding-left: 15px;
}
.admin-left-market-padding{
    font-family:"Poppins";
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 15px;
    color: #586870;
}
.admin-left-market-paddings{
    font-family:"Poppins";
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 15px;
    color: #586870;
}
.admin-left-market-btn{ 
    width: 87px;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-left: 80px;
    margin-top: 30px;
    background: linear-gradient(180deg, rgba(100, 98, 186, 0.666) 0%, rgba(78, 76, 173, 0.707) 100%);
    border-radius: 8px;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    color: #FFFFFF;
    border : 3px solid transparent;
    cursor: pointer;
    transition: 1s;
    margin-bottom: 10px;
}
.admin-left-market-btn:hover{ 
    border: 3px solid  rgba(78, 76, 173, 0.707);
    background: white;
    color: rgba(92, 90, 181, 0.666) ;
    transition: 0.3s;
}
.admin-left-top-left-padding-market-active{
    font-family: "Poppins";
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 120%;
    color: #5957BA;
    width: 120px;
    margin-left: -30px;
    cursor: pointer;
    margin-top: 50px;
    border-bottom: 3px solid #5957BA;
    box-shadow: 1px 20px 10px 0px #5957baac;
    padding-left: 15px;
    padding-right: 10px;
}
.admin-left-top-left-padding-market{
    font-family: "Poppins";
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 120%;
    color: #5957BA;
    width: 120px;
    margin-left: -30px;
