.more{
    display: flex;
    margin-top: 120px;
}
.more-right-padding1{
    text-transform: uppercase;
    font-family: "Poppins";
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    line-height: 60px;
    letter-spacing: 0.05em;
    color: #000000;
    color: #fff;
    border: 3px solid #1eaaac;
    background-color: #1eaaac;
    max-width: 300px;
    padding-left: 10px;
    border-radius: 40px;
}
.more-left{
    margin-right: 70px;
}
.more-left-img{
    border-radius: 25px;
}
.more-right-padding{
    text-transform: uppercase;
    font-family: "Poppins";
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    line-height: 60px;
    letter-spacing: 0.05em;
    color: #1eaaac;
    border: 3px solid #1eaaac;
    padding: 10px;
    border-radius: 40px;
}
.modal-edits{
    padding: 20px;
    position: fixed;
    margin:  0 auto;
    display: flex;
    flex-direction: column;
    text-align: center;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    max-width: 400px;
    z-index: 1000;
    margin-top: 150px;
    background: white;
    border-radius: 10px;
    height: 400px;
}