.main-hero{
    background-image: url(./../../assets/Img/MainBackground.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 1000px;
    bottom: 100px;
    z-index: -1;
    margin: 0 auto;
}
.main-hero-full{
    display: flex;
    justify-content: space-between;
    margin-top: 80px;
}
.main-hero-left-padding1{
    font-family: "Poppins";
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.06em;
    color: #FFFFFF;
    opacity: 0.7;

}
.main-hero-left-padding{
    font-family: "Poppins";
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 25px;
    letter-spacing: 0.15em;
    color: #FFFFFF;

}
.main-hero-left{
    margin-top: 100px;
}
.main-hero-left-heading{
    margin: 0 ;
    padding: 0 ;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 600;
    font-size: 66px;
    line-height: 70px;
    letter-spacing: 0.03em;
    color: #FFFFFF;

}
.main-hero-left-btns{
    width: 300px;
    display: flex;
    justify-content: space-between;
    margin-top: 80px;
}
.main-hero-left-btn1{
    cursor: pointer;
    border: 3px solid transparent;
    padding: 10px;
    background: linear-gradient(265.37deg, #9224D6 -49.74%, #BD87DE 116.53%);
    border-radius: 30px;
    font-family:'Poppins' ;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.03em;
    color: #FFFFFF;
}
.main-hero-left-btn{
    cursor: pointer;
    border: 3px solid white;
    padding: 10px;
    background: transparent;
    border-radius: 30px;
    font-family:'Poppins' ;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.03em;
    color: #FFFFFF;
}
.user-card-heading{
    text-align: center;
    font-family: "Poppins";
    font-style: normal;
    font-weight: normal;
    font-size: 52px;
    line-height: 52px;
    letter-spacing: 0.05em;
    color: #000000;
}
.user-card-market{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.Navlink-card{
    text-decoration: none;
    width: 255px;
    padding: 20px;
    border: 5px solid #626262;
    margin-bottom: 30px;
    margin-left: 10px;
    border: 1.5px solid #6d6d6e;
    border-radius: 10px;
    
}
.card-btn-more{
    text-decoration: none;
    margin-left: 20px;
    background: #6F73EE;
    border-radius: 5px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 50px;
    padding-right: 50px;
    cursor: pointer;
    border: 3px solid transparent;
    font-family: "Poppins";
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 17px;
    text-align: center;
    color: #FFFFFF;
}
.card-btn-more:hover{
    background: #FFFFFF;
    border: 3px solid #6F73EE;
    color:#6F73EE ;
    transition: 0.5s;
}
.card-padding{
    margin:  0;
    padding: 0;
    font-family: "Poppins";
    font-style: normal;
    font-weight: normal;
    font-size: 26px;
    line-height: 52px;
    letter-spacing: 0.05em;
    color: #000000;
}
.card-padding1{
    margin:  0;
    padding: 0;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 52px;
    letter-spacing: 0.05em;
    color: #000000;
}
.card-padding2{
    margin:  0;
    padding: 0;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 0.05em;
    color: #454545;
}
.card-btn1{
    margin: 0;
    padding: 0;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    padding: 5px;
    background-color: transparent;
    border: 2px solid #000000;
    cursor: pointer;
    margin-bottom: 40px;
}
.card-btn{
    margin: 0;
    padding: 0;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    padding: 3px;
    background-color: transparent;
    border: 2px solid #000000;
    cursor: pointer;
}
.card-btns{
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
}
.card-btn{
    z-index: 1;
}
#overlay{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 3;
}
.modal-delet{
    padding: 20px;
    position: fixed;
    margin:  0 auto;
    display: flex;
    flex-direction: column;
    text-align: center;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    max-width: 400px;
    z-index: 1000;
    margin-top: 150px;
    background: rgba(23, 35, 51, 0.7);
    box-shadow: inset 0px 1px 2px rgba(239, 232, 232, 0.4);
    backdrop-filter: blur(30px);
    border-radius: 10px;
    height: 350px;
}
.modal-heading{
    font-family: "Poppins";
    font-style: normal;
    font-weight: 500;
    font-size: 34px;
    line-height: 170%;
    text-transform: capitalize;
    color: #FFFFFF;
}
.modal-btns{
    max-width: 300px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    margin-top: 50px;
}
.modal-btn{
    background: linear-gradient(4.19deg, #f72c2c -17.59%, #cc4e05 115.3%);
    backdrop-filter: blur(5px);
    border-radius: 15px;
    border-radius: 5px;
    margin-left: 20px;
    margin-top: 5px;
    padding: 15px 35px;
    cursor: pointer;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 167.3%;
    color: #DCDCDC;
    border: 1px solid transparent;
    text-decoration: none;
}
.modal-btn-close{
    background: linear-gradient(4.19deg, #11da1b -17.59%, #0ccc05 115.3%);
    backdrop-filter: blur(5px);
    border-radius: 15px;
    border-radius: 5px;
    margin-left: 20px;
    margin-top: 5px;
    padding: 15px 35px;
    cursor: pointer;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 167.3%;
    color: #DCDCDC;
    border: 1px solid transparent;
    text-decoration: none;
}
.modal-edit{
    padding: 20px;
    position: fixed;
    margin:  0 auto;
    display: flex;
    flex-direction: column;
    text-align: center;
    left: 0px;
    right: 0px;
    top: 0px;
    bottom: 0px;
    max-width: 400px;
    z-index: 1000;
    margin-top: 150px;
    background: white;
    border-radius: 10px;
    height: 350px;
}
.admin-left-inputs{
    background: rgba(255, 255, 255, 0.4);
    border: 0.5px solid #C0C6C9;
    box-sizing: border-box;
    border-radius: 8px;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 248px;
    padding-left: 15px;
    margin-bottom: 20px;
}
.admin-left-market-btns{
    width: 87px;
    margin-left: 160px;
    margin-top: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    background: linear-gradient(180deg, rgba(100, 98, 186, 0.666) 0%, rgba(78, 76, 173, 0.707) 100%);
    border-radius: 8px;
    font-family: "Poppins";
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    color: #FFFFFF;
    border : 3px solid transparent;
    cursor: pointer;
    transition: 1s;
    margin-bottom: 10px;
}
.modal-edit-heading{
    font-family: "Poppins";
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 170%;
    text-transform: capitalize;
    color: #000000;
}
.modal-edit-top{
    display: flex;
    justify-content: space-between;
}
.modal-edit-close{
    position: relative;
    bottom: 25px;
    font-size: 24px;
    cursor: pointer;
}
.modal-edit-heading{
    position: relative;
    left: 90px;
}