.header-background{
    background: rgba(122, 134, 177, 0.4);
    backdrop-filter: blur(10px);
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 100;
}
.header-bottom{
    position: absolute;
}
.header{
    display: flex;
    justify-content: space-between;
}
.header-right-item{
    width: 250px;
    display: flex;
    justify-content: space-between;
}
.header-right-item-Navlink{
    font-family: "Poppins";
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.02em;
    color: #FFFFFF;
    opacity: 0.8;
    text-decoration: none;
    margin-top: 10px;
}
.header-right-item-Navlink-active{
    border: 2px solid #FFFFFF;
    font-family: "Poppins";
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.05em;
    color: #FFFFFF;
    text-decoration: none;
    padding: 10px;
    border-radius: 25px;
}
.header-left-img{
    margin-top: 10px;
}